  background-color: transparent;
  max-width: 550px;
  margin: 0px auto;
  color: #262626;
  padding: 30px 0; }

.form-wrapper * {
  outline: none; }

.form__inner {
  display: flex;
  justify-content: space-between; }

.form__body {
  position: relative; }

.form__body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(51, 51, 51, 0.9) url("img/loading.gif") center/50px no-repeat;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease 0s; }

.form__body._sending::after {
  opacity: 1;
  visibility: visible; }

.form__item {
  margin: 0px 0px 20px 0px; }

.form__label {
  font-size: 18px;
  display: block;
  margin: 0px 0px 10px 0px; }

.form__input {
  height: 50px;
  padding: 0px 20px;
  width: 100%;
  font-size: 18px;
  transition: all 0.5s ease 0s;
  border: none;
  border-bottom: 2px solid #255E0F; }

.form__input:focus {
  box-shadow: 0 0 15px #255E0F; }

.form__input._error {
  box-shadow: 0 0 15px red; }

textarea.form__input {
  min-height: 120px;
  resize: vertical;
  padding: 20px; }

.options {
  padding: 10px 0px 0px 0px; }

.options__item {
  margin: 0px 0px 10px 0px; }

.options__input {
  display: none; }

.options__input:checked + .options__label::after {
  transform: scale(1); }

.options__label {
  display: inline-flex;
  font-size: 16px;
  line-height: 140%;
  align-items: center;
  position: relative;
  cursor: pointer; }

.options__label::before {
  content: "";
  align-self: flex-start;
  flex: 0 0 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  margin: 0px 10px 0px 0px; }

.options__label::after {
  transition: transform 0.5s ease 0s;
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 16px;
  height: 16px;
  background-color: #C5AC2D;
  border-radius: 50%;
  transform: scale(0); }

.checkbox._error .checkbox__label::before {
  box-shadow: 0 0 15px red; }

.checkbox__input {
  display: none; }

.checkbox__input:checked + .checkbox__label::after {
  transform: scale(1); }

.checkbox__label {
  width: 100%;
  font-size: 16px;
  line-height: 140%;
  display: inline-flex;
  align-items: center;
  position: relative;
  cursor: pointer; }

.checkbox__label::before {
  content: "";
  align-self: flex-start;
  flex: 0 0 24px;
  height: 24px;
  background-color: #D3E1E2;
  border-radius: 4px;
  margin: 0px 10px 0px 0px;
  border: 1px solid #255E0F; }

.checkbox__label::after {
  transition: transform 0.5s ease 0s;
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  top: 4px;
  left: 4px;
  background-color: #8CC62A;
  border-radius: 4px;
  transform: scale(0); }

.checkbox__label a {
  color: #C5AC2D; }

.form__button {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  background-color: #8CC62A;
  color: #fff;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 700;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0 3px 0 #255E0F;
  transition: background-color 0.5s ease 0s;
  position: relative;
  top: 0; }

.form__button:active {
  top: 3px;
  box-shadow: 0 2px 0 #255E0F; }

.file__item {
  position: relative; }

.file__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  font-size: 0;
  cursor: pointer; }

.file__button {
  display: inline-flex;
  min-height: 40px;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  background-color: #77608d;
  padding: 0 20px;
  cursor: pointer; }

.gallery {
  padding: 70px 0;
  position: relative;
  background: linear-gradient(90deg, #a9ddad 1%, #f5fcf5 35%, #e9f7e9 77%, #a9ddad 100%); }

.gallery__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px; }

.gallery__link {
  position: relative; }

.gallery__link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(122, 122, 122, 0.5);
  background-image: url("../img/svg/icon-search.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transition: opacity 0.3s ease;
  opacity: 0; }

.gallery__link:hover.gallery__link::before {
  opacity: 1; }
